.tab-select-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 32px;
  margin: 0 0 32px 0;
  :nth-child(1) {
    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;
  }
  :nth-child(5) {
    border-top-right-radius: 12px;
    border-bottom-right-radius: 12px;
  }
  .space {
    display: hidden;
    width: 1px;
    height: 100%;
  }
}
